* {
 padding: 0;
 margin: 0;
 box-sizing: border-box;
 list-style: none;
}

:root {
 --color-primary: #191d2b;
 // --color-secondary: #27ae60;
 --color-secondary: #ffd700;
 --color-white: #ffffff;
 --color-black: #000000;
 --color-grey-0: #f8f8f8;
 --color-grey-1: #dbe1e8;
 --color-grey-2: #b2becd;
 --color-grey-3: #6c7983;
 --color-grey-4: #454e56;
 --color-grey-5: #2a2e35;
 --color-grey-6: #12181b;
 --br-sm-2: 14px;
 --box-shadow-1: 0 3px 15px rgba(0, 0, 0, 0.3);
}

.light-mode {
 --color-primary: #ffffff;
 --color-secondary: #f56692;
 --color-white: #454e56;
 --color-black: #000000;
 --color-grey-0: #f8f8f8;
 --color-grey-1: #dbe1e8;
 --color-grey-2: #b2becd;
 --color-grey-3: #6c7983;
 --color-grey-4: #454e56;
 --color-grey-5: #f8f8f8;
 --color-grey-6: #12181b;
}

body {
 background-color: var(--color-primary);
 font-family: 'Poppins', sans-serif;
 font-size: 1.1rem;
 color: var(--color-white);
 transition: all 0.4s ease-in-out;
}

a {
 display: inline-block;
 text-decoration: none;
 color: inherit;
 font-family: inherit;
}

header {
 height: 100vh;
 color: var(--color-white);
 overflow: hidden;

 @media screen and (max-width: 970px) {
  height: unset;
  min-height: 100vh;
 }
}

section {
 min-height: 100vh;
 width: 100%;
 position: absolute;
 left: 0;
 top: 0;
 padding: 3rem 18rem;
}

.section {
 transform: translateY(-100%) scale(0);
 transition: all 0.4s ease-in-out;
 background-color: var(--color-primary);
}

.section-1 {
 display: none;
 transform: translateY(0) scale(1);
}

.section-2 {
 display: none;
 transform: translateY(0) scale(1);
}

.section-3 {
 display: none;
 transform: translateY(0) scale(1);
}

.section-4 {
 display: none;
 transform: translateY(0) scale(1);
}

.section-5 {
 display: none;
 transform: translateY(0) scale(1);
}

.section-6 {
 display: none;
 transform: translateY(0) scale(1);
}

.section-7 {
 display: none;
 transform: translateY(0) scale(1);
}

.active {
 display: block;
 animation: scaleAnim 1s ease-in-out;
 @keyframes scaleAnim {
  0% {
   transform: translateY(-100%) scaleY(0);
  }
  100% {
   transform: translateY(0) scaleY(1);
  }
 }
}

// controls
.controlls {
 position: fixed;
 z-index: 10;
 top: 50%;
 right: 3%;
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: center;
 transform: translate(-50%, -50%);

 .active-btn {
  background-color: var(--color-secondary) !important;
  transition: all 0.4s ease-in-out;

  i {
   color: var(--color-white) !important;
  }
 }

 .control {
  padding: 1rem;
  cursor: pointer;
  background-color: var(--color-grey-4);
  width: 55px;
  height: 55px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.7rem 0;
  box-shadow: var(--box-shadow-1);

  i {
   font-size: 1.2rem;
   color: var(--color-grey-2);
   pointer-events: none;
  }
 }
}

.theme-btn {
 top: 5%;
 right: 3%;
 width: 70px;
 height: 70px;
 border-radius: 50%;
 background-color: var(--color-grey-4);
 cursor: pointer;
 position: fixed;
 display: flex;
 justify-content: center;
 align-items: center;
 box-shadow: 0 3px 15px rgba(0, 0, 0, 0.3);
 transition: all 0.1s ease-in-out;

 &:active {
  transform: translateY(-3px);
 }

 i {
  font-size: 1.4rem;
  color: var(--color-grey-2);
  pointer-events: none;
 }
}

/* Header Content */
.header-content {
 display: grid;
 grid-template-columns: repeat(2, 1fr);
 height: 100%;

 .right-header {
  display: flex;
  align-items: center;
  position: relative;

  .h-shape {
   transition: all 0.4s ease-in-out;
   width: 78%;
   height: 100%;
   background-color: var(--color-secondary);
   position: absolute;
   right: 19%;
   top: 0;
   z-index: -1;
   // clip-path: polygon(0 0, 46% 0, 79% 100%, 0% 100%);
   clip-path: polygon(100% 0, 100% 100%, 21% 100%, 55% 0);
  }

  .image {
   border-radius: var(--br-sm-2);
   height: 90%;
   width: 68%;
   margin-left: 4rem;
   background-color: var(--color-black);
   transition: all 0.4s ease-in-out;

   img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.4s ease-in-out;
    filter: grayscale(100%);

    &:hover {
     filter: grayscale(0);
    }
   }
  }
 }

 .left-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 6rem;
  padding-right: 6rem;

  .name {
   font-size: 3rem;

   span {
    color: var(--color-secondary);
   }
  }

  p {
   margin: 1.5rem 0;
   line-height: 2rem;
  }
 }
}

/* About section */
.about-container {
 display: grid;
 grid-template-columns: repeat(2, 1fr);
 padding-top: 3.5rem;
 padding-bottom: 5rem;

 .right-about {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;

  .about-item {
   border: 1px solid var(--color-grey-5);
   border-radius: 5px;
   transition: all 0.4s ease-in-out;
   box-shadow: 1px 2px 15px rgba(0, 0, 0, 0.1);

   &:hover {
    cursor: default;
    transform: translateY(-5px);
    border: 1px solid var(--color-secondary);
    box-shadow: 1px 4px 15px rgba(0, 0, 0, 0.32);
   }

   .about-text {
    padding: 1.5rem;
    display: flex;
    flex-direction: column;

    .large-text {
     font-size: 3rem;
     font-weight: 700;
     color: var(--color-secondary);
    }

    .small-text {
     padding-left: 3rem;
     position: relative;
     text-transform: uppercase;
     font-size: 1.2rem;
     color: var(--color-grey-1);
     letter-spacing: 2px;

     &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 15px;
      width: 2rem;
      height: 2px;
      background-color: var(--color-grey-5);
     }
    }
   }
  }
 }

 .left-about {
  padding-right: 5rem;

  p {
   line-height: 2rem;
   padding: 1rem;
   color: var(--color-grey-1);
  }

  h4 {
   font-size: 2rem;
   text-transform: uppercase;
  }
 }
}

.about-stats {
 padding-bottom: 4rem;

 .progress-bars {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;

  .progress-bar {
   display: flex;
   flex-direction: column;

   .prog-title {
    text-transform: uppercase;
    font-weight: 500;
   }

   .progress-con {
    display: flex;
    align-items: center;

    .prog-text {
     color: var(--color-grey-2);
    }

    .progress {
     width: 100%;
     height: 0.45rem;
     background-color: var(--color-grey-4);
     margin-left: 1rem;
     position: relative;

     span {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      background-color: var(--color-secondary);
      transition: all 0.4s ease-in-out;
      width: 60%;
     }

     .html {
      width: 60%;
     }

     .css {
      width: 95%;
     }

     .js {
      width: 75%;
     }

     .react {
      width: 60%;
     }

     .node {
      width: 87%;
     }

     .python {
      width: 70%;
     }
    }
   }
  }
 }
}

.stat-title {
 text-transform: uppercase;
 font-size: 1.4rem;
 text-align: center;
 padding: 3.5rem 0;
 position: relative;

 &::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  width: 40%;
  height: 1px;
  background-color: var(--color-grey-5);
  transform: translateX(-50%);
 }
}

/* Timeline */
.timeline {
 display: grid;
 grid-template-columns: repeat(2, 1fr);
 grid-gap: 2rem;
 padding-bottom: 3rem;

 .timeline-item {
  position: relative;
  padding-left: 3rem;
  border-left: 1px solid var(--color-grey-5);

  .tl-icon {
   position: absolute;
   left: -27px;
   top: 0;
   background-color: var(--color-secondary);
   width: 50px;
   height: 50px;
   border-radius: 50%;
   display: flex;
   justify-content: center;
   align-items: center;

   i {
    font-size: 1.3rem;
   }
  }

  .tl-duration {
   padding: 0.2rem 0.6rem;
   background-color: var(--color-grey-5);
   border-radius: 15px;
   display: inline-block;
   font-size: 0.8rem;
   text-transform: uppercase;
   font-weight: 500;
  }

  h5 {
   padding: 1rem 0;
   text-transform: uppercase;
   font-size: 1.3rem;
   font-weight: 600;

   span {
    color: var(--color-grey-2);
    font-weight: 500;
    font-size: 1.2rem;
   }
  }

  p {
   color: var(--color-grey-2);
  }
 }
}

/* Portfolios */
.port-text {
 padding: 2rem 0;
 text-align: center;
}

.portfolios {
 display: grid;
 grid-template-columns: repeat(3, 1fr);
 grid-gap: 2rem;
 margin-top: 3rem;

 .portfolio-item {
  position: relative;
  border-radius: 15px;

  img {
   width: 100%;
   height: 300px;
   object-fit: cover;
   border-radius: 15px;
  }

  .hover-items {
   width: 100%;
   height: 100%;
   background-color: var(--color-secondary);
   position: absolute;
   left: 0;
   top: 0;
   border-radius: 15px;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   opacity: 0;
   transform: scale(0);
   transition: all 0.4s ease-in-out;

   h3 {
    font-size: 1.5rem;
    color: var(--color-white);
    margin-bottom: 1.5rem;
   }

   .icons {
    display: flex;
    justify-content: center;
    align-items: center;

    .icon {
     background-color: var(--color-primary);
     border-radius: 50%;
     width: 50px;
     height: 50px;
     display: flex;
     justify-content: center;
     align-items: center;
     margin: 0 1rem;
     cursor: pointer;
     transition: all 0.4s ease-in-out;

     i {
      font-size: 1.5rem;
      color: var(--color-white);
      margin: 0 1rem;
     }

     &:hover {
      background-color: var(--color-white);

      i {
       color: var(--color-primary);
      }
     }
    }
   }
  }
 }
}

.portfolio-item:hover .hover-items {
 opacity: 0.75;
 transform: scale(1);
}

/* Blogs Section */
.blogs {
 display: grid;
 grid-template-columns: repeat(3, 1fr);
 grid-gap: 2rem;
 margin-top: 3rem;

 .blog {
  position: relative;
  background-color: var(--color-grey-5);
  border-radius: 5px;
  box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.1);
  transition: all 0.4s ease-in-out;

  &:hover {
   box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.3);
   transform: translateY(-5px);
   transition: all 0.4s ease-in-out;

   img {
    filter: grayscale(0);
    transform: scale(1.1);
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.8);
   }
  }

  img {
   width: 100%;
   height: 300px;
   object-fit: cover;
   border-top-left-radius: 5px;
   border-top-right-radius: 5px;
   filter: grayscale(100%);
   transition: all 0.4s ease-in-out;
  }

  .blog-text {
   margin-top: -7px;
   padding: 1.1rem;
   border-top: 8px solid var(--color-secondary);

   h4 {
    font-size: 1.5rem;
    margin-bottom: 0.7rem;
    transition: all 0.4s ease-in-out;
    cursor: pointer;

    &:hover {
     color: var(--color-secondary);
    }
   }

   p {
    color: var(--color-grey-2);
    line-height: 2rem;
    padding-bottom: 1rem;
   }
  }
 }
}

/* Contact Section */
.contact-content-container {
 display: flex;
 padding-top: 3.5rem;

 .left-contact {
  flex: 2;

  h4 {
   margin-top: 1rem;
   font-size: 2rem;
   text-transform: uppercase;
  }

  p {
   margin: 1rem 0;
   line-height: 2rem;
  }

  .contact-info {
   .contact-item {
    display: flex;
    align-items: center;
    justify-content: space-between;

    p {
     margin: 0.3rem 0 !important;
     padding: 0 !important;
    }

    .icon {
     display: grid;
     grid-template-columns: 40px 1fr;

     i {
      display: flex;
      align-items: center;
      font-size: 1.3rem;
     }
    }
   }
  }

  .contact-icon {
   display: flex;
   margin-top: 2rem;

   a {
    display: flex;
    align-items: center;
    color: var(--color-white);
    background-color: var(--color-grey-5);
    cursor: pointer;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 0 0.4rem;
    transition: all 0.4s ease-in-out;

    &:hover {
     background-color: var(--color-secondary);

     i {
      color: var(--color-primary);
     }
    }

    i {
     display: flex;
     align-items: center;
     justify-content: center;
     font-size: 1.3rem;
     transition: color 0.4s ease-in-out;
    }
   }
  }
 }

 .right-contact {
  flex: 3;
  margin-left: 3rem;

  .input-control {
   margin: 1.5em 0;

   input, textarea {
    border-radius: 30px;
    font-weight: inherit;
    font-size: inherit;
    font-family: inherit;
    padding: 0.8rem 1.1rem;
    outline: none;
    border: none;
    background-color: var(--color-grey-5);
    width: 100%;
    color: var(--color-white);
    resize: none;
   }
  }

  .i-c-2 {
   display: flex;

   :last-child {
    margin-left: 1.5rem;
   }
  }

  .submit-btn {
   display: flex;
   justify-content: flex-start;
  }
 }
}

/* Independed Components */
.btn-container {
 display: flex;
 align-self: flex-start;
}

.main-btn {
 border-radius: 30px;
 color: inherit;
 font-weight: 600;
 position: relative;
 border: 1px solid var(--color-secondary);
 display: flex;
 align-self: flex-start;
 align-items: center;
 overflow: hidden;

 .btn-text {
  padding: 0 2rem;
 }

 .btn-icon {
  background-color: var(--color-secondary);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  padding: 1rem;
 }

 &::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(100%);
  transition: all 0.4s ease-in-out;
  z-index: -1;
 }

 &:hover {
  &::before {
   width: 100%;
   height: 100%;
   background-color: var(--color-secondary);
   transform: translateX(0);
  }
 }
}

.main-title {
 text-align: center;

 h2 {
  position: relative;
  text-transform: uppercase;
  font-size: 4rem;
  font-weight: 700;

  span {
   color: var(--color-secondary);
  }

  .bg-text {
   position: absolute;
   top: 50%;
   left: 50%;
   color: var(--color-grey-5);
   transition: all 0.4s ease-in-out;
   z-index: -1;
   transform: translate(-50%, -50%);
   font-weight: 800;
   font-size: 6.3rem;
  }
 }
}